
html, body, #root,#App, #App>div {
  width:fit-content;
  height: 100%
}

body {
  overscroll-behavior-y: contain; /* https://stackoverflow.com/questions/36212722/how-to-prevent-pull-down-to-refresh-of-mobile-chrome */
}
.explanation {
  font-size: larger;
  word-wrap: break-word;
    max-height: 300px;
    overflow: auto;
}

#legendDiv,#growthRateLegendDiv,#projLegendDiv,#maxVelLegendDiv,#tampPLegendDiv,#tampTLegendDiv {
  padding-top: 1.5%;
  padding-bottom: 3.5%;
  display: flex;
  word-wrap: break-word;
  flex-wrap: wrap;
  justify-content: space-evenly;
  position: relative;
  top: 20px
}




.dygraph-legend-line,.dygraph-legend-dash {
  display: inline-block;
  position: relative;
  top: 1ex;
  padding-left: 1em;
  height: 5px;
  border-bottom-width: 4px;
  border-bottom-style: solid;
  left: 2%;
}

.containerLegend {
  color: white;
  background: #2c2e35;
}



.footer{
  height: 50px;
  width: 100%;
  background:#2c2e35;
  color:#FFF;
  text-align:center;
  position:absolute;
  bottom:0
}


.generalTabs{
  position: fixed;
  width: 100%;
  z-index: 999;
  background: #FFF;
}
